@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
}

html, body, #root {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #0a0a0f;
}

/* Note: Removed CSS rotation - please rotate your phone to landscape manually */
/* CSS transform breaks touch events on iOS */

body {
  font-family: 'Orbitron', sans-serif;
  background: #0a0a0f;
  color: white;
}

.controller {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 8px;
  background: linear-gradient(135deg, #0a0a0f 0%, #1a1a2f 100%);
  gap: 6px;
  overflow: hidden;
}

/* HEADER */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5px;
  flex-shrink: 0;
}

.title {
  font-size: 1rem;
  font-weight: 900;
  background: linear-gradient(90deg, #0f0, #0ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.connect-btn {
  padding: 10px 20px;
  font-family: 'Orbitron', sans-serif;
  font-size: 0.8rem;
  font-weight: 700;
  border: 2px solid #0cf;
  background: transparent;
  color: #0cf;
  border-radius: 20px;
  cursor: pointer;
  flex-shrink: 0;
}

.connect-btn.connected {
  border-color: #0f0;
  color: #0f0;
  background: rgba(0, 255, 0, 0.1);
}

.connect-btn:active {
  transform: scale(0.95);
}

/* MAIN CONTROLS */
.main-controls {
  flex: 1;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  gap: 8px;
  padding: 0 5px;
  min-height: 0;
}

/* ARROW BUTTONS */
.arrow-btn {
  width: 70px;
  flex-shrink: 0;
  touch-action: none;
  -webkit-touch-callout: none;
  font-size: 2rem;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.arrow-btn.left {
  background: linear-gradient(135deg, #0cf, #06a);
  color: white;
  box-shadow: 0 4px 15px rgba(0, 204, 255, 0.3);
}

.arrow-btn.right {
  background: linear-gradient(135deg, #0cf, #06a);
  color: white;
  box-shadow: 0 4px 15px rgba(0, 204, 255, 0.3);
}

.arrow-btn:active {
  transform: scale(0.95);
}

/* CENTER PANEL */
.center-panel {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  min-width: 0;
}

.speed-section {
  display: flex;
  align-items: center;
  gap: 8px;
  height: 100%;
}

.slider-container {
  width: 35px;
  height: 100%;
  position: relative;
}

.slider {
  writing-mode: vertical-lr;
  direction: rtl;
  width: 100%;
  height: 100%;
  -webkit-appearance: none;
  background: transparent;
  position: absolute;
  z-index: 10;
  cursor: pointer;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 32px;
  height: 20px;
  background: linear-gradient(135deg, #f80, #f50);
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(255,136,0,0.5);
}

.slider::-moz-range-thumb {
  width: 32px;
  height: 20px;
  background: linear-gradient(135deg, #f80, #f50);
  border-radius: 5px;
  border: none;
}

.slider-bg {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 16px;
  height: 100%;
  background: #050508;
  border-radius: 8px;
  border: 2px solid #333;
  overflow: hidden;
}

.slider-fill {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: linear-gradient(to top, #f80, #f33);
}

.speed-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.speed-num {
  font-size: 0.9rem;
  font-weight: 900;
  color: #f80;
}

.stop-btn {
  padding: 10px 14px;
  font-family: 'Orbitron', sans-serif;
  font-size: 0.7rem;
  font-weight: 700;
  background: #f33;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 3px 10px rgba(255, 50, 50, 0.4);
}

.stop-btn:active {
  transform: scale(0.95);
}

/* STEERING INFO */
.steering-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.steering-value {
  font-size: 1.4rem;
  font-weight: 900;
  color: #0ff;
  min-width: 50px;
  text-align: center;
}

.reset-btn {
  padding: 8px 12px;
  font-family: 'Orbitron', sans-serif;
  font-size: 0.7rem;
  font-weight: 700;
  background: transparent;
  color: #0f0;
  border: 2px solid #0f0;
  border-radius: 6px;
  cursor: pointer;
}

.reset-btn:active {
  background: #0f0;
  color: #000;
}

/* STEERING BAR */
.steering-bar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 0 5px;
  flex-shrink: 0;
}

.bar-label {
  font-size: 0.6rem;
  color: #666;
  min-width: 25px;
}

.bar-label.right {
  text-align: right;
}

.bar-track {
  flex: 1;
  height: 12px;
  background: #151520;
  border-radius: 6px;
  position: relative;
  border: 1px solid #333;
}

.steering-indicator {
  position: absolute;
  top: 50%;
  width: 18px;
  height: 18px;
  background: linear-gradient(135deg, #0ff, #0f0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 10px #0ff;
}

/* TERMINAL */
.terminal {
  background: #050508;
  border: 1px solid #222;
  border-radius: 4px;
  padding: 4px 8px;
  font-family: monospace;
  font-size: 0.5rem;
  min-height: 30px;
  max-height: 40px;
  overflow: hidden;
  flex-shrink: 0;
}

.terminal div {
  color: #555;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.3;
}

.terminal div.out { color: #f80; }
.terminal div.in { color: #0cf; }

/* Extra small screens */
@media (max-height: 400px) {
  .controller {
    padding: 5px;
    gap: 4px;
  }
  
  .title {
    font-size: 0.9rem;
  }
  
  .connect-btn {
    padding: 8px 14px;
    font-size: 0.7rem;
  }
  
  .arrow-btn {
    width: 60px;
    font-size: 1.6rem;
  }
  
  .steering-value {
    font-size: 1.2rem;
  }
  
  .terminal {
    display: none;
  }
}
